import React, { Fragment } from 'react';
import styled from 'styled-components';
import { rem } from 'polished';
import { Header } from '../style/typography';

const PortfolioWrapper = styled.div`
	display: grid;
	grid-template-columns: auto auto;
	grid-template-rows: 500px 500px;
	grid-gap: ${rem('32px')};
`;

const PortfolioItemWrapper = styled.div`
	border: 1px solid rgba(0, 0, 0, 0.8);
	padding: 20px;
`;

const PortfolioItemHeader = styled.h3`
	font-weight: normal;
	font-size: ${rem('32px')};
`;

const PortfolioItemDescription = styled.p`
	color: ${(props) => props.theme.color.neutral.dark};
	margin: 0;
`;

const PortfolioItem = ({ image, title, description }) => (
	<PortfolioItemWrapper>
		<img src={image} />
		<PortfolioItemHeader>{title}</PortfolioItemHeader>
		<PortfolioItemDescription>{description}</PortfolioItemDescription>
	</PortfolioItemWrapper>
);

export default () => (
	<Fragment>
		<Header>My Recent Projects</Header>
		<PortfolioWrapper>
			<PortfolioItem
				image="../../static/sentimind.png"
				title="SentiMind"
				description="A Twitter sentiment analysis tool, built over 9 days as part of the Makers Academy bootcamp course"
			/>
		</PortfolioWrapper>
	</Fragment>
);
